	module.exports = {"data":{"markdownRemark":{"html":"<p>Looks like some people have been having difficulty with deviantART recently. The site has been loading, but not the CSS.</p>\n<p>What was the issue? Well here's the bug report...</p>\n<blockquote>\n<p>I just wanted to let you know that the reason why deviantART's CSS isn't loading properly for some people is because one of your CSS files has f*** in a stylesheet comment.</p>\n</blockquote>\n<p>Yes, a comment in the CSS that included the word \"fuck\" was being picked up by over overzealous filtering systems, resulting in the file being blocked, and not loaded.</p>\n<p>What was the fix? Well besides removing the comment from the file, deviantART have now moved to use <a href=\"http://lesscss.org\">LESS</a>, a CSS preprocessor.</p>\n<p>This isn't a blog post to talk about the benefits of CSS preprocessors, but as deviantART inform us they cannot stop their developers from swearing in comments, this should stop these developer comments from appearing in served css files again.</p>\n<p><small>Source <a href=\"http://dt.deviantart.com/journal/We-Give-a-F-How-the-Site-Loads-392679726\">deviantART</a></small></p>","timeToRead":1,"excerpt":"Looks like some people have been having difficulty with deviantART recently. The site has been loading, but not the CSS. What was the issue…","frontmatter":{"title":"We Give a F*** How the Site Loads","cover":"https://unsplash.it/1152/300/?random?FuckCss","date":"2013-08-13T22:57","tags":["code","quote post"],"author":"eddie"},"fields":{"slug":"/we-give-a-f-how-the-site-loads"}},"prev":{"excerpt":"Ever since Ethan was born people have been asking me questions along the lines of,  \"what does it feel like to…","frontmatter":{"title":"What's it like to be a dad?","cover":"https://unsplash.it/1152/300/?random?Dad","date":"2013-08-20T08:01"},"fields":{"slug":"/whats-it-like-to-be-a-dad"}},"next":{"excerpt":"On June 18th 2013 my son was born - this was me becoming a dad at the age of 22. At this point, just over one…","frontmatter":{"title":"Fatherhood","cover":"https://unsplash.it/1152/300/?random?Fatherhood","date":"2013-07-31T23:29"},"fields":{"slug":"/fatherhood"}},"authors":{"edges":[{"node":{"id":"eddie","name":"Eddie Lee","image":"/images/eddielee.jpg","url":"https://twitter.com/eddielee6","bio":"Father and Software Engineer"}}]}},"pathContext":{"slug":"/we-give-a-f-how-the-site-loads","total":45,"prev":"/whats-it-like-to-be-a-dad","next":"/fatherhood"}}

